<template>
  <div class="manage-three">
    <!--  -->
    <div class="manage-add">
      <el-button type="primary" size="mini" @click="addNew"></el-button>
      <el-button type="success" size="mini" @click="getList"></el-button>
    </div>
    <div class="tabs">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="fileheadings"
          label="">
        </el-table-column>
        <el-table-column
          prop="filetype"
          label="">
        </el-table-column>
        <el-table-column
          prop="filepeople"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="time"
          label=""
          width="160">
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageNum"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addNewDialog"
      :close-on-click-modal="false"
      top="2%"
      width="40%">
      <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="110px" class="demo-ruleForm">
        <el-form-item label="" prop="fileheadings">
          <el-input type="text" v-model="ruleForm.fileheadings" placeholder=""></el-input>
        </el-form-item>
        <!-- <img src="../../../assets/image/fileType.png" alt="" style="margin-left: 30px;margin-bottom: 20px;" /> -->
        <el-form-item label="">
          <el-upload
            class="upload-demo"
            :action="action"
            :class="hasFile ? 'fileicon' : ''"
            :before-upload="beforeImgUpload"
            :on-remove="handleRemove"
            :on-success="handleImgSuccess"
            :on-exceed="handleExceed"
            multiple
            :limit="1"
            name="file"
            :file-list="ruleForm.fileList">
            <el-button size="small" type="primary"></el-button><div slot="tip" class="el-upload__tip"> doc / docx / ppt / pptx / xls / xlsx / pdf / txt / png / jpg / jpeg / rar / zip </div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')"> </el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="deleteDialog"
      :close-on-click-modal="false"
      width="30%">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog = false"> </el-button>
        <el-button type="primary" @click="delSure"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {success, notice} from '@/api/index'
import url from '@/baseUrl'
// import {dateFormat} from '@/filters/index'
export default {
  name: 'manage-three',
  data () {
    return {
      tableData: [],
      ruleForm: {
        fileheadings: '',
        fileenclosure: '',
        fileList: []
      },
      rules2: {
        fileheadings: [
          { required: true, trigger: 'blur', message: '' }
        ]
        // authorofthzarticle: [
        //   { required: true, trigger: 'blur', message: '' }
        // ],
        // fileList: [
        //   { type: 'array', required: true, message: '1', trigger: 'change' }
        // ]
      },
      addNewDialog: false,
      deleteDialog: false,
      currentPage: 0,
      pageNum: 10,
      totalCount: 0,
      dialogImageUrl: '',
      fileUrl: '',
      filename: '',
      isAdmin: false,
      hasFile: false,
      inifoId: ''
    }
  },
  computed: {
    action () {
      return url + '/upload'
    }
  },
  methods: {
    handleDelete (index, row) {
      this.inifoId = row.id
      this.deleteDialog = true
    },
    addNew () {
      this.ruleForm.fileheadings = ''
      this.ruleForm.fileList = []
      this.hasFile = false
      this.addNewDialog = true
    },
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          await this.$store.dispatch('fileAdd', {
            fileheadings: this.ruleForm.fileheadings,
            filepeople: this.$store.state.username,
            fileenclosure: this.fileUrl,
            filename: this.filename,
            username: this.$store.state.accountname
          })
        } else {
          return false
        }
        this.addNewDialog = false
        this.ruleForm.fileList = []
        this.hasFile = false
        this.ruleForm.fileheadings = ''
        this.getList()
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.addNewDialog = false
    },
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    handleRemove (file, fileList) {
      fileList = []
      this.fileUrl = ''
      this.filename = ''
      this.hasFile = false
    },
    handleExceed (files, fileList) {
      notice('')
    },
    handleImgSuccess (res, file, fileList) {
      this.fileUrl = res ? res.data : ''
      this.filename = res ? res.path : ''
      success('')
      this.hasFile = true
    },
    // beforeImgUpload (file) {
    //   return new Promise(async (resolve, reject) => {
    //     const type = file.name.split('.')[file.name.split('.').length - 1].toLowerCase()
    //     if ((type === 'doc' || type === 'docx' || type === 'ppt' || type === 'pptx' || type === 'xls' || type === 'xlsx') && file.size / 1024 / 1024 > 20) {
    //       this.$message.error('OFFICE20M')
    //       reject(new Error('OFFICE20M'))
    //       return
    //     }
    //     if (type === 'pdf' && file.size / 1024 / 1024 > 10) {
    //       this.$message.error('pdf 10M')
    //       reject(new Error('pdf 10M'))
    //       return
    //     }
    //     if ((type === 'jpg' || type === 'png' || type === 'gif' || type === 'jpeg') && file.size / 1024 / 1024 > 10) {
    //       this.$message.error('10M')
    //       reject(new Error('10M'))
    //       return
    //     }
    //     if (type === 'txt' && file.size / 1024 / 1024 > 1) {
    //       this.$message.error('1M')
    //       reject(new Error('1M'))
    //       return
    //     }
    //     if (type === 'epub' && file.size / 1024 / 1024 > 20) {
    //       this.$message.error('20M')
    //       reject(new Error('20M'))
    //       return
    //     }
    //     if ((type === 'mp3' || type === 'acc' || type === 'mp4' || type === 'mpg' || type === 'avi' || type === 'wmv') && file.size / 1024 / 1024 > 200) {
    //       this.$message.error('200M')
    //       reject(new Error('200M'))
    //       return
    //     }
    //     if (file.size / 1024 / 1024 > 200) {
    //       this.$message.error('200M')
    //       reject(new Error('200M'))
    //       return
    //     }
    //     if (type !== 'mp3' && type !== 'acc' && type !== 'mp4' && type !== 'mpg' && type !== 'avi' && type !== 'wmv' &&
    //         type !== 'epub' && type !== 'txt' && type !== 'jpg' && type !== 'png' && type !== 'gif' && type !== 'jpeg' &&
    //         type !== 'pdf' && type !== 'doc' && type !== 'docx' && type !== 'ppt' && type !== 'pptx' && type !== 'xls' &&
    //         type !== 'xlsx' && type !== 'exe' && type !== 'rar' && type !== 'zip') {
    //       this.$message.error('')
    //       reject(new Error(''))
    //       return
    //     }
    //     resolve()
    //   })
    // },
    beforeImgUpload (file) {
      return new Promise(async (resolve, reject) => {
        const type = file.name.split('.')[file.name.split('.').length - 1].toLowerCase()
        if ((type === 'pdf' || type === 'doc' || type === 'docx' || type === 'ppt' || type === 'pptx' || type === 'xls' || type === 'xlsx') && file.size / 1024 / 1024 > 200) {
          this.$message.error('200M')
          reject(new Error('200M'))
          return
        }
        if ((type === 'jpg' || type === 'png' || type === 'gif' || type === 'jpeg') && file.size / 1024 / 1024 > 5) {
          this.$message.error('5M')
          reject(new Error('5M'))
          return
        }
        if (type === 'txt' && file.size / 1024 / 1024 > 2) {
          this.$message.error('2M')
          reject(new Error('2M'))
          return
        }
        // if (type === 'epub' && file.size / 1024 / 1024 > 20) {
        //   this.$message.error('20M')
        //   reject(new Error('20M'))
        //   return
        // }
        // if ((type === 'mp3' || type === 'acc' || type === 'mp4' || type === 'mpg' || type === 'avi' || type === 'wmv') && file.size / 1024 / 1024 > 200) {
        //   this.$message.error('200M')
        //   reject(new Error('200M'))
        //   return
        // }
        if (file.size / 1024 / 1024 > 200) {
          this.$message.error('200M')
          reject(new Error('200M'))
          return
        }
        if (type !== 'txt' && type !== 'jpg' && type !== 'png' && type !== 'gif' && type !== 'jpeg' &&
            type !== 'pdf' && type !== 'doc' && type !== 'docx' && type !== 'ppt' && type !== 'pptx' && type !== 'xls' &&
            type !== 'xlsx' && type !== 'rar' && type !== 'zip') {
          this.$message.error('')
          reject(new Error(''))
          return
        }
        resolve()
      })
    },
    async getList () {
      this.tableData = []
      await this.$store.dispatch('fileSelect', {
        pageNum: this.currentPage,
        pageSize: this.pageNum,
        username: this.isAdmin ? '' : this.$store.state.accountname
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          if (this.$store.state.userid && this.$store.state.userid.split(',').length === 7) {
            this.tableData = res.list.map((item, i) => ({
              ...item,
              filetype: item.filename ? ('.' + item.filename.split('.')[item.filename.split('.').length - 1]) : ''
            }))
          } else {
            this.tableData = res.list.map(item => ({
              ...item,
              filetype: item.filename ? ('.' + item.filename.split('.')[item.filename.split('.').length - 1]) : ''
            }))
          }
          this.totalCount = res.total
        }
      })
    },
    async delSure () {
      await this.$store.dispatch('fileDel', {
        groupId: this.inifoId
      }).then((res) => {
        this.deleteDialog = false
        this.getList()
      })
    }
  },
  created () {
    let xi = this.$store.state.userid.split(',')
    if (xi && xi.length === 7) {
      this.isAdmin = true
    } else {
      this.isAdmin = false
    }
    this.getList()
  }
}
</script>

<style lang="scss">
.manage-three {
  text-align: left;
  .manage-add {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #efefef;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .tabs {
    padding: 20px;
    .el-table th:first-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    th {
      background: #f5f7fa;
    }
  }
  .el-form {
    .el-input {
      width: 90%;
    }
  }
  .el-pagination {
    margin-top: 20px;
    text-align: right;
  }
  .fileicon {
    .el-upload {
      display: none;
    }
    .el-upload__tip {
      margin-top: 0;
    }
  }
}
</style>



