<template>
    <div class="hello">
        <div id="editor">
        </div>
    </div>
</template>

<script>
import E from 'wangeditor'
export default {
  data () {
    return {
      content: '',
      edit: ''
    }
  },
  props: ['mode'],
  methods: {
    clea () {
      this.edit.txt.clear()
    },
    change () {
      this.$nextTick(() => {
        this.clea()
        this.edit.txt.html(this.mode)
      })
    }
  },
  created () {
    if (this.mode) {
      this.change()
    }
  },
  mounted () {
    let _this = this
    var editor = this.edit = new E('#editor')
    editor.customConfig.uploadImgShowBase64 = true
    // alert
    editor.customConfig.customAlert = function (info) {
      // info 
      alert('' + info)
    }
    editor.customConfig.menus = [ 
      'head', //  
      'bold', //  
      'fontSize', //  
      'fontName', //  
      'italic', //  
      'underline', //  
      'strikeThrough', //  
      'foreColor', //  
      'backColor', // 
      'list', //  
      'justify', //  
      'quote', //  
      'image', //  
      'table', //  
      'code', //  
      'undo', //  
      'redo' //  
    ] 
    editor.customConfig.onchange = function (info) {
      _this.content = info
      _this.$emit('cont', info)
    }
    editor.create()
    // 
    // editor.txt.html('<p> JS </p>')
    // 
    // editor.txt.clear()
  }
}
</script>



